import Lines from '@/components/common/Lines.vue'
import {mapGetters} from 'vuex'
export default {
	data() {
		return {
			path: false
		}
	},
	computed: {
		...mapGetters(['defaultPath'])
	},
	components: {
		Lines
	},
	onLoad() {
		if (this.defaultPath.length) {
			this.path = this.defaultPath[0]
		}
		//触发自定义事件  on接收
		
		uni.$on('SelectPathItem',(data)=>{
		       this.path=data
		    })
	},
	onUnload() {
		uni.$off('SelectPathItem',()=>{
			console.log('移除SelectPathItem')
		})
	},
	methods: {
		//跳转地址管理
		goPathList() {
			uni.navigateTo({
				url: '../my-path-list/my-path-list?type=selectedPath'
			})
		},
		//进入支付页面
		goPament(){
			uni.navigateTo({
				url:"../payment/payment"
			})
			}
	}
}
